axios.defaults.baseURL = "https://lastarzi2.herokuapp.com";
//axios.defaults.baseURL = "http://localhost:5000";



const params = {
  effect: "cube",
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true,
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: ".swiper-pagination"
  }
}

class PasswordVault extends React.Component {
  state = {
    active: "1",
    modal: false,
    pwd: '',
    ptype: '',
    confirmPwd: '',
    saved: [
      {
      pwdname: 'Hey',
      pwd: 'asdasd',
      showPass: false,
    },
    {
      pwdname: 'Hey 212',
      pwd: 'asdaasdsa121sd',
      showPass: false,
    }
  ]
  }

  componentWillMount() {

    axios
      .post("/backendapi/pwd/list",{user: JSON.parse(localStorage.getItem('logInUserData'))._id})
      .then(res => {
        this.setState({saved:res.data});
      })

  }

  toggleModal = () => {
    
    this.setState(prevState => ({
      modal: !prevState.modal,
      confirmPwd: '', showIdx: ''
    }))
  }

  toggleModal2 = () => {
    if(this.state.confirmPwd === '123') {
      console.log('this.state.showIdx', this.state.showIdx);
      this.togglepwdshow(true, this.state.showIdx);
      setTimeout(function(){ this.togglepwdshow(false,this.state.showIdx); }.bind(this), 5000)
      this.setState(prevState => ({
        modal: !prevState.modal,
        confirmPwd: '',
      }))
    } else {
      toast.warning('Wrong Password');
    }
  }

  addToSaved=(e)=> {
    e.preventDefault();
    const {saved, ptype, pwd} = this.state;
    const newp = {
      pwdname: ptype,
      pwd,
      showPass: false,
    }
    const newp1 = {
      pwdname: ptype,
      pwd,
      user:  JSON.parse(localStorage.getItem('logInUserData'))._id
    }
    saved.push(newp);
    this.setState({saved,pwd:'', ptype: ''});
    axios
      .post("/backendapi/savepwd",newp1)
      .then(res => {
        //this.setState({saved:res.data});
      })
  }

  togglepwdshow = (b,i) =>{
    const {saved} = this.state;
    console.log(b,i);
    saved[i].showPass = b;
    
    this.setState({saved});
  } 

  deletepwd = (i) =>{
    const {saved} = this.state;
    saved.pop(saved[i]);
    this.setState({saved});
  } 

  render() {
    return (
      <React.Fragment>
         <Modal 
        isOpen={this.state.modal}
        toggle={this.toggleModal}
        className="modal-dialog-centered"
        fade={false}
      >
        <ModalHeader style={{border:'solid 1px'}} toggle={this.toggleModal}>
          Confirm your identity
        </ModalHeader>
        <ModalBody style={{border:'solid 1px'}} >
          Enter your login password to continue
          <br />
          <br />
          <FormGroup>
            {/* <Label for="password">Password:</Label> */}
            <Input
              type="password"
              id="password"
              placeholder="Password"
              onChange={(e)=>{this.setState({confirmPwd: e.target.value})}}
            />
          </FormGroup>
          <Alert className="mt-1" color="danger">
                      <span>
                       Your password will only be visible for 3 seconds!
                      </span>
                    </Alert>
        </ModalBody>
        <ModalFooter>
          <Button color={this.state.confirmPwd === '' ? "primary": "success"} disabled={this.state.confirmPwd === '' } onClick={this.toggleModal2}>
            Accept
          </Button>{" "}
        </ModalFooter>
      </Modal>
        <Breadcrumbs
          breadCrumbTitle="My Vault"
          breadCrumbParent="Password"
          breadCrumbActive="Vault"
        />
         <Row>
         <Col lg="8" md="6" sm="12">
         
        <div style={{width: window.screen.width < 500 ? '100%' : '150%'}}> 
       
        <Row>
        <Col  sm="12">
    <TabContent activeTab={this.state.active}>
      <TabPane tabId="1">
      
      <Row>
        <ToastContainer  />
        <Col lg="8" md="6" sm="12">
      <Card>
        <CardHeader>
          <CardTitle>Save new Password</CardTitle>
        </CardHeader>
        <CardBody>
          <Form className="mt-2">
          
            <Row>
            <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="name"
                    id="nameMulti"
                    placeholder="Password Type"
                    value={this.state.ptype}
                    onChange={(e)=>{this.setState({ptype:e.target.value })}}
                  />
                  <Label for="nameMulti">Password Type</Label>
                </FormGroup>
              </Col>
              <Col md="4" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="password"
                    name="name"
                    id="nameMulti"
                    placeholder="Password"
                    value={this.state.pwd}
                    onChange={(e)=>{this.setState({pwd:e.target.value })}}
                  />
                  <Label for="nameMulti">Password</Label>
                </FormGroup>
              </Col>
              <Col md="2" sm="12">
                <FormGroup className="form-label-group">
                  <Button.Ripple
                    color="primary"
                    type="submit"
                    className="mr-1 mb-1"
                    //disabled={this.state.nominees.length === 0}
                    onClick={(e)=>{this.addToSaved(e)}}
                  >
                    Add
                  </Button.Ripple>
                  </FormGroup>
                  </Col>
            
              
            </Row>
          </Form>
        </CardBody>
      </Card>
      </Col>
      











        
      </Row>





      <Row>
        <ToastContainer  />
        <Col lg="8" md="6" sm="12">
      <Card>
        <CardHeader>
          <CardTitle>Saved Passwords</CardTitle>
        </CardHeader>
        <CardBody>
          <Form className="mt-2">
          {this.state.saved.map((p,idx)=> {
            return (
              <Row>
            <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="name"
                    id="nameMulti"
                    disabled
                    value={p.pwdname}
                    //placeholder="First Name"
                  />
                  <Label for="nameMulti">Password Type</Label>
                </FormGroup>
              </Col>
              <Col md="4" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type={p.showPass ? "text" : "password"}
                    name="name"
                    id="nameMulti"
                    placeholder="Password"
                    disabled
                    value={p.pwd}
                  />
                  <Label for="nameMulti">Password</Label>
                </FormGroup>
              </Col>
              <Col md="2" sm="12">
            
                <FormGroup className="form-label-group">
                {!p.showPass && <EyeOff size={20} onClick={()=>{this.setState({showIdx:idx,modal: !this.state.modal});  }} />}
                  {p.showPass && <Eye size={20} onClick={()=>{this.togglepwdshow(false,idx)}} />}
                  <Trash style={{marginLeft: '10px'}} size={20} className="mr-4 fonticon-wrap" onClick={()=>{this.deletepwd(idx)}} />
                  </FormGroup>
                  </Col>
            
          
            </Row>
            );
          })}
            
          </Form>
        </CardBody>
      </Card>
      </Col>
      
        
      </Row>
      </TabPane>
      <TabPane tabId="2">
     
      </TabPane>
      <TabPane tabId="3">
        Coming soon .......!
      </TabPane>
    </TabContent>
            
          </Col>
        </Row>
        </div>
        </Col>
        <Col lg="4" md="6" sm="12">
        <div>
         










          

    <Swiper {...params}>
      <div
        style={{
          backgroundImage: `url(${img1})`,
          backgroundSize: "contain"
        }}
      ></div>
      <div
        style={{
          backgroundImage: `url(${img2})`,
          backgroundSize: "contain"
        }}
      ></div>
      <div
        style={{
          backgroundImage: `url(${img3})`,
          backgroundSize: "contain"
        }}
      ></div>
      <div
        style={{
          backgroundImage: `url(${img4})`,
          backgroundSize: "contain"
        }}
      ></div>
      <div
        style={{
          backgroundImage: `url(${img5})`,
          backgroundSize: "contain"
        }}
      ></div>
    </Swiper>
  


        </div>
        </Col>
        </Row>
      </React.Fragment>
    )
  }
}
export default PasswordVault



